<template>
    <div class="screen">
        <div id="cardholder">
            <div class="card"  v-on:click="select(0)" v-bind:style="{ transform: shift, background:cardcolor[0] }">
                <div class="cardpic" >
                    <img id="kep" :src='cards[hand[0]].url'></img>
                </div>
                <span>{{cards[hand[0]].name}}</span><br>
                <span>Tmads: {{cards[hand[0]].attack}}</span><br>
                <span>let: {{cards[hand[0]].life}}</span><br>
                <span>r: {{cards[hand[0]].cost}}</span>
            </div>
            <div class="card" v-on:click="select(1)" v-bind:style="{ transform: shift, background:cardcolor[1] }">
                <div class="cardpic">
                    <img id="kep" :src='cards[hand[1]].url'></img>
                </div>
                <span>{{cards[hand[1]].name}}</span><br>
                <span>Tmads: {{cards[hand[1]].attack}}</span><br>
                <span>let: {{cards[hand[1]].life}}</span><br>
                <span>r: {{cards[hand[1]].cost}}</span>
            </div>
            <div class="card" v-on:click="select(2)"  v-bind:style="{ transform: shift, background:cardcolor[2] }">
                 <div class="cardpic">
                     <img id="kep" :src='cards[hand[2]].url'></img>
                 </div>
                 <span>{{cards[hand[2]].name}}</span><br>
                 <span>Tmads: {{cards[hand[2]].attack}}</span><br>
                 <span>let: {{cards[hand[2]].life}}</span><br>
                 <span>r: {{cards[hand[2]].cost}}</span>
            </div>
            <progress id="timebar" max="100" :value="loadbar" > 70% </progress>
            <h3>{{round}}round</h3>
            <button id="responsebutton" v-on:click="sendResponse">Send response</button>
            <div id="ownstatholder">
                <img class="staticons" :src='icons.Life.url' style="width:20px !important;height:20px !important;float:left !important"><span class="ownstatlife">Life: {{life}}</span><br><br>
                <img class="staticons" :src='icons.Coin.url' style="width:20px !important;height:20px !important;float:left !important"><span class="ownstatlife">Gold: {{gold}}</span>
            </div>
        </div>
        <div id="stats">
            <div class="columns">
            <p>Player2</p>
            <img class="staticons"  :src='icons.Coin.url' style="width:20px !important;height:20px !important;float:left"></img>Gold:{{player2.gold}} <br>
            <img class="staticons"  :src='icons.Life.url' style="width:20px !important;height:20px !important;"></img>Life:{{player2.life}} <br>
            <img class="staticons"  :src='icons.Deck.url' style="width:20px !important;height:20px !important;">Deck:{{player2.deck}} <br>
            <img class="staticons"  :src='icons.Graveyard.url' style="width:20px !important;height:20px !important;">Graveyard:{{player2.graveyard}}
            </div>
            <div class="columns">

            </div>
            <div class="columns">

            </div>
        </div>
        <div id="history">
            <div class="historyheader">
                    History
            </div>
            <div class="historycolumns">
                <span>Attack:{{ownhistorystat.attack}}</span>
                <span>Defense:{{ownhistorystat.defense}}</span>
                <div v-for="cardid in ownhistory">
                    <img id="kep" :src='cards[cardid].url' style="width:40px !important;"></img>
                </div><br>
                <span>Damage:{{ownhistorystat.damage}}</span>
            </div>
            <div class="historycolumns">
                <span>Attack:{{opponenthistorystat.attack}}</span>
                <span>Defense:{{opponenthistorystat.defense}}</span>
                <div v-for="cardid in opponenthistory">
                    <img id="kep" :src='cards[cardid].url' style="width:40px !important;"></img>
                </div><br>
                <span>Damage:{{opponenthistorystat.damage}}</span>
            </div>
        </div>
    </div>

</template>
<script>
let socket=require('../socketmanager.js')
let cardbase=require('../clientcards.js').Cards
let icons=require('../icons.js').Icons
export default {
    data: function(){
        return{
            ownhistory:[1,1,3],
            opponenthistory:[2,2],
            ownhistorystat:{"attack":2,
                            "defense":2,
                            "damage":2
                            },
            opponenthistorystat:{"attack":2,
                                 "defense":2,
                                 "damage":2
            },
            icons:icons,
            shift:'translateX(0px)',
            round:'Fight',
            cardcolor:{
                    0:'gray',
                    1:'gray',
                    2:'gray'},
            selected:[],
            cards:cardbase,
            hand:[0,1,2],
            life:20,
            gold:5,
            deck:3,
            graveyard:0,
            loadbar:100,
            timer:'',
            ownid:'',
            player2:{
                gold:5,
                life:20,
                deck:3,
                graveyard:3
            }

        }
    },
    methods: {
        shiftCards:function(){
            if(this.shift=='translateX(0px)'){
                this.shift='translateX(-1200px)'
            }
            else
            {
                this.shift='translateX(0px)'
            }
        },
        roundChange:function(){
            this.shiftCards()
            if(this.round=='Buy'){
                this.cardcolor[0]='coral'
                this.cardcolor[1]='coral'
                this.cardcolor[2]='coral'
            }
            else
            {
                this.cardcolor[0]='gray'
                this.cardcolor[1]='gray'
                this.cardcolor[2]='gray'
            }
            
            setTimeout(()=>this.shiftCards(),1100)
        },
        select:function(index){
            if(this.cardcolor[index]=='green'){
                //unselecting card
                this.gold+=this.cards[this.hand[index]].cost
                this.selected=this.removeElement(this.selected,index)
                if(this.round=="Fight"){
                    this.cardcolor[index]='gray'
                }
                else
                {
                    this.cardcolor[index]='coral'
                }
            }
            else
            {
                //Selecting new card
                if(this.round=="Fight"){
                    if(this.cards[this.hand[index]].cost<=this.gold){
                       this.gold=this.gold-this.cards[this.hand[index]].cost
                       this.cardcolor[index]='green'
                       this.selected.push(index)
                    }
                }
                else
                {
                    //first selection for buy round
                    if(this.selected.length==0){
                        if(this.cards[this.hand[index]].cost<=this.gold){
                            this.gold=this.gold-this.cards[this.hand[index]].cost
                            this.cardcolor[index]='green'
                            this.selected.push(index)
                        }
                    }
                    else
                    {
                        //second selection for buy round
                        let totalgold=this.cards[this.hand[this.selected[0]]].cost+this.gold
                        if(this.cards[this.hand[index]].cost<=totalgold){
                            let cardcolorkeys=Object.keys(this.cardcolor)
                            cardcolorkeys.forEach(element => {
                                this.cardcolor[element]='coral'
                            });
                            this.gold+=this.cards[this.hand[this.selected[0]]].cost
                            this.gold=this.gold-this.cards[this.hand[index]].cost
                            this.cardcolor[index]='green'
                            this.selected=[index]
                        }
                    }
                }
            }
        },
        removeElement:function(array,element){
            for( var i = 0; i < array.length; i++){ 
                if ( array[i] == element) {
                    array.splice(i, 1); 
                }
            }
            return array;
        },
        sendResponse:function(){
            socket.socket.emit('response',this.selected)
        },
        returnImgSrc:function(index){
            return this.cardTexts[index].img
        },
        cardhandle:function(){
            socket.socket.on('hand',data=>{
                if(this.round=='Fight')
                {
                    this.hand=data
                    this.loadbar=100
                        clearInterval(this.timer)
                        this.timer=setInterval(()=>{
                            this.loadbar--
                        },120)
                    setTimeout(()=>{
                        this.selected.forEach(element => {
                            this.gold=this.gold+this.cards[hand[element]].cost
                        });
                    },12000)
                }
                else
                {
                    //this.gold=this.gold+5
                    this.selected=[]
                    this.round='Fight'
                    this.roundChange()
                    setTimeout(()=>{
                      this.hand=data;
                      this.loadbar=100
                        clearInterval(this.timer)
                        this.timer=setInterval(()=>{
                            this.loadbar--
                        },120)
                    },1000)
                    setTimeout(()=>{
                        this.selected.forEach(element => {
                            this.gold=this.gold+this.cards[this.hand[element]].cost
                        });
                    },12000)
                }
            })
        },
        buyhandle:function(){
            socket.socket.on('buyround',data=>{
                this.selected=[]
                this.round='Buy'
                this.roundChange()
                setTimeout(()=>{
                    this.hand=data;
                    this.loadbar=100
                    clearInterval(this.timer)
                    this.timer=setInterval(()=>{
                        this.loadbar--
                    },120)
                },1000)
                setTimeout(()=>{
                        this.selected.forEach(element => {
                            this.gold=this.gold+this.cards[this.hand[element]].cost
                        });
                    },12000)
                
            })
        },
        playerstats:function(){
            socket.socket.on('status',data=>{
                console.log(data)
                Object.keys(data).forEach(key=>{
                    if(key!=socket.socket.id){
                        this.player2.gold=data[key].Gold
                        this.player2.life=data[key].Life
                        this.player2.graveyard=data[key].Graveyard
                        this.player2.deck=data[key].Deck
                    }
                    else
                    {
                        this.life=data[key].Life
                        this.gold=data[key].Gold
                        this.graveyard=data[key].Graveyard
                        this.deck=data[key].Deck
                    }
                })
            })
        },
        setOwnID:function(){
            this.ownid=socket.socket.id
        },
        victory:function(){
            socket.socket.on('victory',data=>{
                console.log("vicotryyy")
                alert("You win!")
                this.$router.push('/')
            })
        },
        lostgame:function(){
            socket.socket.on('lost',data=>{
                alert("You lost")
                this.$router.push('/')
            })
        },
        surrender:function(){
            console.log('sent surrender')
            //socket.socket.emit('surrender')
            this.$router.push('/')
        },
        history:function(){
            socket.socket.on('history',data=>{
                if(data.p1.id==socket.socket.id){
                    this.ownhistory=data.p1.deck
                    this.ownhistorystat.attack=data.p1.attack
                    this.ownhistorystat.defense=data.p1.defense
                    this.ownhistorystat.damage=data.p1.damage

                    this.opponenthistory=data.p2.deck
                    this.opponenthistorystat.attack=data.p2.attack
                    this.opponenthistorystat.defense=data.p2.defense
                    this.opponenthistorystat.damage=data.p2.damage
                }
                else
                {
                    this.ownhistory=data.p2.deck
                    this.ownhistorystat.attack=data.p2.attack
                    this.ownhistorystat.defense=data.p2.defense
                    this.ownhistorystat.damage=data.p2.damage

                    this.opponenthistory=data.p1.deck
                    this.opponenthistorystat.attack=data.p1.attack
                    this.opponenthistorystat.defense=data.p1.defense
                    this.opponenthistorystat.damage=data.p1.damage
                }
            })
        }

    },
    created(){
        this.cardhandle()
        this.buyhandle()
        this.setOwnID()
        this.playerstats()
        this.victory()
        this.lostgame()
        this.history()
    }
}
</script>
<style scoped>
.columns{
    width: 33.33%;
    float:left;
    height:100%;
}
.historycolumns{
    width:50%;
    float:left;
    height:90%;
}
.historycolumns span{
    display:block;
    text-align: center;
    font-size:20px;;
}
.historycolumns img{
    height:40px;
    float:left;
    margin-left:5%;
}
.historyheader{
    height:10%;
    background-color: brown;
    text-align: center;

}
.staticons{
    float:left;
    width:10px;
    height:50px;
}

.ownstatlife{
    float:left;
    display: block;
}

#ownstatholder{
    float:left;
    width:40%;
}

h3{
    margin:0px;
}

#responsebutton{
    float:right;
    height: 20%;
    width:30%;
}

.card{
    width: 30%;
    height: 55%;
    background-color: gray;
    display: inline-block;
    margin:1%;
    vertical-align: top;
    border-radius: 8px;
    transition: 1s ease;
}
#vala{
    background-color: brown
}
#cardholder{
    background-color: lightblue;
    height:40%;
    text-align:center;
}

.screen{
    height: 100%;
}

#kep{
    width: 100%;
    max-height: 100%;
}
.cardpic{
    width:100%;
    height:40%;
}

#stats{
    height:30%;
    background: olive;
    font-size: 20px;
}

.ownstat{
    float:left;
    font-weight: bold;
}
@media screen and (max-height: 560px) {
    .card {
      background-color: lightgreen;
      font-size: small;
    }
    #responsesend{
        width:30%;
        height:20px;
        font-size: small;
    }
  }
</style>